
.message {
  max-width: 300px;
}
.message .message-header .card-actions a {
    text-decoration: none;
}
.message .message-body {
    word-break: break-word;
    text-align: left;
}
.message .message-body .tags {
      margin-bottom: 1em;
}
.message .message-body .task-text {
      margin-bottom: 1em;
}
.message .message-body .task-text h1, .message .message-body .task-text h2, .message .message-body .task-text h3, .message .message-body .task-text h4, .message .message-body .task-text h5, .message .message-body .task-text ul {
        margin: .2em 0;
}
.message .message-body .task-text h1 {
        font-size: 1.5em;
}
.message .message-body .task-text h2 {
        font-size: 1.35em;
}
.message .message-body .task-text h3 {
        font-size: 1.2em;
}
.message .message-body .task-text h4 {
        font-size: 1.05em;
}
.message .message-body .task-text h5 {
        font-size: 1em;
}
.message .message-body .task-text ul {
        margin-left: 1.2em;
}



