    margin: .75rem;
}
.detail .description h1 {
    font-size: 2em;
}
.detail .description h2 {
    font-size: 1.5em;
}
.detail .description h3 {
    font-size: 1.17em;
}
.detail .description h4 {
    font-size: 1.12em;
}
.detail .description h5 {
    font-size: .83em;
}
.detail .description h6 {
    font-size: .75em;
}
.detail .description ul {
    list-style: disc;
}



